.todo-list {
    list-style: none;

    &__item {
        max-width: 100%;
        border: 2px solid black;
        border-radius: 20px;
        padding: 10px 10px;

        margin-bottom: 15px;

        &-title {
            text-align: center;
        }

        &-text {
            width: 100%;
            max-width: 100%;
            word-wrap: break-word;
        }
    }
}